.mbtilayout{
  width: 100vw;
  font-size: 14px;
  display: flex;
  justify-content: center;
  background-color: #eee;
  height: calc(var(--vh, 1vh)*100);
}


.startPageLayout{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #128AFE;
  width: 100%;
  max-width: 900px;
  height: 100%;
  color: white;
}


.startLogo{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 25px;
  font-weight: 700;
}

.startLogo div:nth-child(1){
  background-color: white;
  color: #128AFE;
  width: 100px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
}
.startLogo div:nth-child(2){
  margin: 0 auto;
  margin-top: -15px;
}

.startButton{
  font-size: 17px;
  font-weight: 700;
  margin: 20px;
}




.questionLayout{
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 100%;
  max-width: 900px;
  height: 100%;
  overflow: hidden;
}
.mbtiTitle{
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  padding: 15px;
  box-sizing: border-box;
  font-weight: 700;
}

.mbtiTitle div:nth-child(1){
  display: flex;
  flex: 1;
}
.questionList{
  flex-direction: column;
  height: 100%;
  overflow: scroll;
  scrollbar-width: none;
}

.questionList::-webkit-scrollbar{
  display: none;
}



.questionItemLayout{
  display: flex;
  flex: 1;
  margin: 10px 15px 5px 15px;
}
                
.answerItemLayout{
  width: 100%;
  background-color: #E5E5EA;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
}

.profileImg{
  display: flex;
  flex-direction: column;
  width: 40px;
  height: 40px;
  background-color: black;
  border-radius: 15px;
  margin-right: 5px;
  align-items: center;
  justify-content: center;
}

.profileImg div:nth-child(1){
  background-color: #eee;
  width: 8px;
  height: 8px;
  border-radius: 4px;
}

.profileImg div:nth-child(2){
  background-color: #eee;
  width: 16px;
  height: 8px;
  border-radius: 8px 8px 0px 0px;
  margin-top: 2px;
}


.chatListLayout{
  display: flex;
  flex-direction: column;
}
.chatBox{
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 60vw;
  margin: 3px 0px 12px 0px;
}

.chatBox div:nth-child(1){
  color: #E5E5EA;
  font-size: 12px;
  margin-top: 8px;
}

.chatBox div:nth-child(2){
  padding: 7px 10px 7px 10px;
  background-color: #E5E5EA;
  margin-left: -4px;
  border-radius: 10px;
  text-align: left;
}


.aChatBox{
  display: flex;
  width: 100%;
  color: #858E99;
  background-color: #E5E5EA;
  font-size: 22px;
  border-bottom: 1px solid #858e9962;
  margin-bottom: 25px;
}

.aChatBox div:nth-child(1){
  display: flex;
  flex: 1;
  padding: 10px;
  box-sizing: border-box;
}

.aChatBox div:nth-child(2){
  padding: 10px;
}

.answerBox{
  background-color: #128AFE;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  text-align: center;
  box-shadow: 2px 2px 0px rgba(128, 128, 128, 0.24);
  /* border: 1px solid #eee; */
  margin: 0px 20px 15px 20px;
  border-radius: 20px;
}